.color-picker {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px; }

.color-palette {
  border-radius: 2px;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.4);
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-width: 0px;
  padding: 0px; }

.color-picker-dialog {
  position: absolute;
  transition: 0.3s all;
  opacity: 0;
  z-index: 1;
  margin: 10px 0px;
  border-radius: 2px;
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.4);
  background-color: #ededed;
  width: 0px;
  height: 0px;
  overflow: hidden; }
  .color-picker-dialog.show {
    opacity: 1; }
  .color-picker-dialog.top {
    top: auto;
    bottom: 100%; }
  .color-picker-dialog.bottom {
    top: 100%;
    bottom: auto; }
  .color-picker-dialog.left {
    right: 0%;
    left: auto; }
  .color-picker-dialog.right {
    right: auto;
    left: 0%; }
  .color-picker-dialog .color-picker-panel {
    width: 100%;
    height: 100%; }
  .color-picker-dialog .color-board {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 65px;
    left: 0px;
    cursor: crosshair; }
    .color-picker-dialog .color-board .color-board-gradient {
      position: absolute;
      background-size: 100% 100%;
      width: 100%;
      height: 100%;
      overflow: hidden; }
    .color-picker-dialog .color-board .color-board-pointer {
      position: absolute;
      margin-top: -5px;
      margin-left: -5px;
      border: 1px solid #ffffff;
      border-radius: 10px;
      box-shadow: 0px 0px 2px 0px #000000;
      background-color: transparent;
      width: 10px;
      height: 10px; }
    .color-picker-dialog .color-board .color-board-overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      background: none;
      border-width: 0px;
      padding: 0px; }
  .color-picker-dialog .hue-banner,
  .color-picker-dialog .alpha-banner {
    position: absolute;
    left: 3%;
    width: 94%;
    height: 15px;
    border-width: 0px;
    padding: 0px;
    border-radius: 2px;
    box-shadow: 0px 0px 2px 0px #ffffff;
    cursor: pointer; }
    .color-picker-dialog .hue-banner .thumb,
    .color-picker-dialog .alpha-banner .thumb {
      position: absolute;
      top: -5px;
      bottom: -5px;
      margin-left: -2px;
      border: 1px solid #ffffff;
      border-radius: 4px;
      box-shadow: 0px 0px 2px 0px #000000;
      background-color: transparent;
      cursor: pointer;
      width: 2px;
      height: auto; }
  .color-picker-dialog .hue-banner {
    bottom: 40px;
    background-size: 100% 100%; }
  .color-picker-dialog .alpha-banner {
    bottom: 10px;
    background-size: contain; }
    .color-picker-dialog .alpha-banner .alpha-banner-overlay {
      border-radius: 2px;
      width: 100%;
      height: 100%; }
